//      css,   jpg \ png,   svg

//    mixin hover-focus      
@mixin hover-focus {
  @media (hover: hover) {
    &:hover:not(.focus-visible) {
      @content;
    }
  }

  &.focus-visible:focus {
    @content;
  }
}

/*  @include hover-focus {
      opacity: 0.8;
    }
*/
//       ( input, textarea )
//         hover
@mixin hover {
  @media (hover: hover) {
    &:hover:not(.focus-visible) {
      @content;
    }
  }
}

//  focus
@mixin focus {
  &.focus-visible:focus {
    @content;
  }
}

//   active
@mixin active {
  &.focus-visible:active {
    @content;
  }
}

